import { Component, OnInit, OnDestroy, ViewChild, ChangeDetectorRef } from '@angular/core';
import { DataService } from 'src/app/_services/data.services';
import Swal from 'sweetalert2';
declare var $: any;

@Component({
  selector: 'app-Home-cmp',
  templateUrl: './Home.component.html'
})

export class HomeComponent implements OnInit, OnDestroy {
  test: Date = new Date();
  step = 0;
  plans = null;

  images = [
    { path: './assets/Images/Darrel Sing.png' },
    { path: './assets/Images/Darrel Sing.png' },
    { path: './assets/Images/Darrel Sing.png' },
    { path: './assets/Images/Darrel Sing.png' },
    { path: './assets/Images/Darrel Sing.png' },
    { path: './assets/Images/Darrel Sing.png' },
    { path: './assets/Images/Darrel Sing.png' },
    { path: './assets/Images/Darrel Sing.png' }
  ];

  imagesForSlider = [
    { path: './assets/Images/Darrel Sing.png' },
    { path: './assets/Images/Darrel Sing.png' },
    { path: './assets/Images/Darrel Sing.png' },
    { path: './assets/Images/Darrel Sing.png' },
    { path: './assets/Images/Darrel Sing.png' },
    { path: './assets/Images/Darrel Sing.png' },
    { path: './assets/Images/Darrel Sing.png' }
  ];

  constructor(
    private cdf: ChangeDetectorRef,
    private dataService: DataService) {}

  ngOnInit() {
    this.dataService.get('host/get_plan').then((result) => {
      this.plans = result.data
      this.cdf.detectChanges()
    })

    const body = document.getElementsByTagName('body')[0];
    body.classList.add('lock-page');
    body.classList.add('off-canvas-sidebar');
    const card = document.getElementsByClassName('card')[0];
    setTimeout(function () {
    }, 700);
  }

  ngOnDestroy() {
    const body = document.getElementsByTagName('body')[0];
    body.classList.remove('lock-page');
    body.classList.remove('off-canvas-sidebar');
  }

  openSignin(){
    Swal.fire({
      position: 'center',
      icon: 'error',
      text: 'Please create your account first. If you already have an account, please sign in.',
      showConfirmButton: false,
      timer: 2500
    })
  }

}
